<!--按照日分类的睡眠-->
<template>
    <div class="sleep-day">
        <p class="date-picker">
            <van-icon name="arrow-left" size="22" color="#999" @click="subtractDay"/>
            <span>{{ date.split('-').join('/') }}</span>
            <van-icon name="arrow" size="22" color="#999" @click="addDay"/>
        </p>
        <sleep-night :onClick="goSleepNight"
                     :on-click-label="onClickLabel"
                     :data="sleepData"
                     :hour="sleepData.fallsleepduration?sleepData.fallsleepduration/60:0"
                     :min="sleepData.fallsleepduration?sleepData.fallsleepduration%60:0"
                     :labels="labels"
        />
        <div>
            <p class="p-data">
                <img src="@/assets/track-sleep/img.png" class="img-data" alt="">
                <span class="span-title">数据解读</span>
            </p>
            <p class="p-data-read">
                您本日的睡眠情况良好，好的睡眠习惯有助于保证睡眠质量，良好的睡眠有助于保护人的身心健康，提升机体免疫力，消除人体疲劳。因此，请继续保持良好的睡眠习惯
            </p>
        </div>
        <!--    <track-plan :src_item="require('@/assets/ic_sleepchange.png')" :title="'推荐计划'"/>-->
        <sleep-help/>
        <!--    <track-explore :title="'睡眠探索'" :sleepExploreList="sleepExploreList"/>-->
    </div>
</template>

<script>
  import {reactive, onMounted, toRefs,watch} from 'vue'
  import SleepNight from '../SleepNightItem'
  import TrackPlan from '../../TrackPlan'
  import SleepHelp from '../sleep-help'
  import TrackExplore from '../../TrackExplore'
  import ChangeDate from '../../components/ChangeDate'
  import {useRouter, useRoute} from 'vue-router'

  const {HOST_H5} = require("../../../../utils/axios");
  const {getHistorySleepByDay} = require("../../../../service/sleep");
  import moment from 'moment'
  import {Toast} from "vant";

  export default {
    name: "index",
    components: {
      SleepNight,
      // TrackPlan,
      SleepHelp,
      // TrackExplore,
      // ChangeDate,
    },
    props: {
      subtractDay: {
        type: Function,
        default: () => {
        }
      },
      addDay: {
        type: Function,
        default: () => {

        }
      },
      date: {
        type: String,
        default: '',
      },
    },
    setup(props) {
      const initData = async () => {//根据日获取数据
        const res = await getHistorySleepByDay(props.date)
        state.sleepData = res.data[0] || ''
        state.sleepAllData = res.data
        //夜间睡眠总时间
        let sumdata = 0
        //小睡总时间
        let xiaodata = 0
        //小睡次数
        let numdata = 0
        for (let i = 0; i < res.data.length; i++) {
          if (res.data[i].sleepType == 0) {
            sumdata += res.data[i].fallsleepduration;
          } else {
            numdata++
            xiaodata += res.data[i].fallsleepduration;
          }
        }
        state.xiaodata = xiaodata
        //设置标签栏数据
        state.labels[0].timeslot = numdata + '次'
        state.labels[1].timeconsumption = sumdata / 60 + '小时' + sumdata % 60 + '分钟'

      }
      onMounted(
        () => {
          initData(props.date)
        }
      )
      watch(()=>props.date,(news,old)=>{
        initData()
      })
      const router = useRouter();
      //跳转夜间睡眠
      const goSleepNight = () => {
        goTo('/sleep-night',{data:state.sleepAllData})
      }
      const onClickLabel = (item) => {
        console.log(333, item)
        if (item.title === '今天睡眠总时长') {
          goTo('/sleep-record')
        } else if (item.title === '白天小睡') {
          let url = HOST_H5 + `/sleep/#/daySleep/0-45/${parseInt(state.xiaodata / 60 + '')}/${parseInt(state.xiaodata % 60 + '')}/${state.xiaodata > 45 ? 2 : 1}`;
          goTo('/sporadic-nap', {url})
        }
      }
      const goTo = (r, query) => {
        router.push({path: r, query: query || {}})
      }
      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        selectdate: new Date(),
        sleepData: '',
        sleepAllData: '',
        xiaodata: 0,
        labels: [
          {
            title: '白天小睡',
            timeslot: '',
            timeconsumption: '',
            status: '',
            imgsrc: require('@/assets/ic_sun.png'),
            backcolor: '#FFF6DF'
          },
          {title: '今天睡眠总时长', timeslot: '', timeconsumption: '', status: '', imgsrc: '', backcolor: '#EAFFFB'},
        ],
      })
      const onchange = (data) => {
        state.selectdate = data
      }
      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        onchange,
        goSleepNight,
        onClickLabel,
      }
    }
  }
</script>

