    .sleep-day {
        height: 100%;
        background: #fff;
        overflow-y: scroll;
    }

    .p-data {
        padding: 15px 10px 0 10px;

        .img-data {
            display: inline-block;
            width: 13px;
            height: 13px;
            margin-right: 10px;
        }

        .span-title {
            font-size: 16px;
            color: #333333;
        }
    }

    .p-data-read {
        padding: 5px 10px 0 10px;
        font-size: 14px;
        text-align: justify;
        text-justify: inter-ideograph;
    }

</style>
