<!--模块通用标题-->
<template>
    <div class="div_moduletitle_content">
        <img  :src="src" style="width: 13px;height: 13px" v-show="src"/>
        <span class="span_title">{{text}}</span>
    </div>
</template>
<script>
  import {reactive, onMounted,toRefs} from 'vue'

  export default {
    name: "ModuleTitle",
    props: {
      type: {
        type: Number,
        default: 0
      },
      src: {
        type: String,
        default: ''
      },
      text: {
        type: String,
        default: ''
      },
    },
    components: {},
    setup() {
      onMounted(
        () => {
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        mData: [0,1,2,3]
      })

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
      }
    }
  }
</script>
<style scoped lang="less">
    .div_moduletitle_content {
        display: flex;
        align-items: center;
        padding-left: 10px;
        margin-top: 15px;
    }
    .span_title{
        font-family: PingFangSC-Medium;
        font-size: 16px;
        color: #333333;
        line-height: 16px;
        margin-left: 5px;
        flex: 9;
    }
</style>
