<!--夜间睡眠-->
<template>
  <div class="div_content" :style="mbackground" @click="onClick">
    <div class="div_night_title">
      <img src="@/assets/ic_night.png"/>
      <span>夜间睡眠</span>
    </div>
    <div class="div_circle_content">
      <circle-progress
          :id="1"
          :width="236"
          :radius="12"
          :progress="data.fallsleepduration?parseInt(data.fallsleepduration/7.2+''):0"
          :delay="200"
          :duration="1000"
          :barColor="'#A0E3D4'"
          :backgroundColor="'#093F2E'"
          :isAnimation="true"
          :isRound="false"
          :timeFunction="'cubic-bezier(0.99, 0.01, 0.22, 0.94)'"
      />
      <div class="div_clockdial">
        <clock-dial/>

      </div>
      <div class="div_circle_des">
        <span class="span_circle_title">夜间睡眠</span>
        <span class="span_circle_des">{{hour}}<span class="span_num">hr</span>{{min}}<span
            class="span_num">min</span></span>
      </div>
    </div>

    <van-icon class="svg_right" color="#fff" size="20px" name="arrow"/>
<!--    <span class="span_num">70<span class="span_num2">分</span></span>-->
    <track-type :datas="['严重不足','睡眠不足','正常睡眠','过量睡眠']" :type="data.type == 0 ? 1:data.type == 1 ? 3:data.type == 2 ? 2:data.type == 3 ? 4:0" :style="{marginTop: '30px',marginBottom:'10px'}" :textstyle="{color:'#ffffff !important'}"/>

    <span class="span_des">您昨晚的睡眠时长是{{hour}}小时{{min}}分<span>「点击查看详情」</span></span>
  </div>

  <sleep-night-label v-for="(item,index) in mlabels" :key="index"
                     :title="item.title"
                     :timeslot="item.timeslot"
                     :timeconsumption="item.timeconsumption"
                     :status="item.status"
                     :imgsrc="item.imgsrc"
                     :backcolor="item.backcolor"
                     :on-click-label="()=>onClickLabel(item)"
  />

</template>

<script>
import {reactive, onMounted, toRefs,watch} from 'vue'
import CircleProgress from '@/components/CircleProgress'
import ClockDial from '@/components/ClockDial'
import SleepNightLabel from './SleepLabel'
import TrackType from '../components/TrackType'

export default {
  name: "index",
  props:{
    onClick:{
      type: Function,
      default:()=>{}
    },
    onClickLabel:Function,
    data:Object,
    hour:String,
    min:String,
    labels:Object,
  },
  components: {
    CircleProgress,
    ClockDial,
    SleepNightLabel,
    TrackType,
  },
  setup(props) {
    onMounted(
        () => {
          state.mlabels = props.labels
        }
    )

    watch(()=>props.labels,(news,old)=>{
      state.mlabels = news
    })
    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      todo: '',
      mbackground: {
        backgroundImage: "url(" + require("@/assets/ic_circle_bg.png") + ")"
      },
      mlabels:[],
    })

    // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
    return {
      ...toRefs(state),
    }
  }
}
</script>

