<!--探索-->

<template>
  <div>
    <module-title :src="src" :text="title"/>
    <div :style="{ height: from === 'drink'? '10px': '0px'}"></div>
    <template v-for="(item,index) in sleepExploreList" :key="index" class="box_rxplore">
      <div class="div-list"  :style="{ backgroundColor: backgroundColor}">
        <div class="div-wrap">
          <img  alt="" :src="item['url']" class="explore-img">
          <div class="div-explore-right">
            <span class="explore-title">{{ item.title }}</span>
            <div class="div-explore-right-bottom">
              <div class="div-explore-right-bottom-wrap">
                    <span class="span-left">{{ item.source }}</span>
                    <p>
                      <svg aria-hidden="true" class="svg"><use xlink:href="#icon-liulan"></use></svg>
                      <span>{{ item.eyes }}</span>
                    </p>
                  <span class="span-right">{{ item.date }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import ModuleTitle from './components/ModuleTitle'
import {reactive, toRefs} from 'vue'

export default {
  name: "TrackExplore",

  props: {
    backgroundColor:{
      type: String,
      default: '',
    },
    from:{
      type: String,
      default: '',
    },
    src: {//图标资源
      type: String,
      default: ''
    },
    title: { //标题
      type: String,
      default: ''
    },
    data: {
      type: String,
      default: '暂无数据'
    },
    sleepExploreList: {
      type: Array,
      default: []
    }
  },
  components: {
    ModuleTitle,
  },
  setup(props) {
    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      todo: '',
      sleepExploreList: []
    })
    state.sleepExploreList = props.sleepExploreList

    // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
    return {
      ...toRefs(state),
    }
  }
}
</script>

