<!--助眠服务-->
<template>
    <module-title :text="'助眠服务'"/>
    <div class="div_sleephelp">
        <div class="div_sleephelp_item" v-for="(item,index) in mdata" :key="index" @click="onClick()">
            <img :src="item.source">
            <span class="span_title">{{item.title}}</span>
            <span class="span_des">{{item.des}}</span>
        </div>
    </div>
</template>

<script>
  import ModuleTitle from '../../components/ModuleTitle'
  import {reactive, onMounted, toRefs} from 'vue'
  import {useRouter} from 'vue-router'

  export default {
    name: "index",
    props: {},
    components: {
      ModuleTitle,
    },
    setup() {
      const userRouter = useRouter()
      onMounted(
        () => {
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        mdata: [{title: '冥想静心', des: '睡前舒缓放松，改善夜间易醒', source: require('@/assets/ic_service_left.png')},
          {title: '深度睡眠引导练习', des: '睡前舒缓放松，改善夜间易醒', source: require('@/assets/ic_service_right.png')}]

      })
      //跳转播放音乐
      const onClick = () => {
        userRouter.push('/sleep-music')
      }
      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        onClick,
      }
    }
  }
</script>

