<!--睡眠入口-->
<template>
  <div class="Sleep">
    <!--头部-->
    <s-header :name="'睡眠跟踪'" :imgForward="require('@/assets/ic_group.png')" rightName="本人" :rightPress="rightPress"/>
    <!--设备选择-->
    <change-device  fontColor="#06B48D" />
    <div class="div_content">
      <van-tabs
          background="#f5f6f9"
          :color="'#1baeae'"
          :title-active-color="'#1baeae'"
      >
        <van-tab title="日">
          <SleepDay :date="date" :subtract-day="subtractDay" :add-day="addDay"/>
        </van-tab>
        <van-tab title="周">
          <SleepWeek :start-week="startWeek" :end-week="endWeek" :subtract-week="subtractWeek" :add-week="addWeek"/>
        </van-tab>
        <van-tab title="月">
          <SleepMonth :start-month="startMonth" :end-month="endMonth" :subtract-month="subtractMonth" :add-month="addMonth"/>

        </van-tab>
        <van-tab title="年">
          <SleepYear :start-year="startYear" :end-year="endYear" :subtract-year="subtractYear" :add-year="addYear"/>

        </van-tab>

      </van-tabs>
    </div>
  </div>
</template>

<script>
import sHeader from '@/components/SimpleHeader'
import ChangeDevice from '../components/ChangeDevice'
import {reactive, onMounted, toRefs} from 'vue'
import SleepDay from './sleep-day'
import SleepWeek from './sleep-week'
import SleepMonth from './sleep-month'
import SleepYear from './sleep-year'
import moment from "moment";

export default {
  name: "index",
  components: {
    ChangeDevice,
    sHeader,
    SleepDay,
    SleepWeek,
    SleepMonth,
    SleepYear
  },
  setup() {

    onMounted(
        () => {
        }
    )

    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const rightPress = () => {

    }
    const state = reactive({
      index: 0,
      startWeek: moment().subtract(6, 'days').format('YYYY-MM-DD'),
      endWeek: moment().format('YYYY-MM-DD'),
      startMonth: moment().subtract(1, 'months').format('YYYY-MM-DD'),
      endMonth: moment().format('YYYY-MM-DD'),
      startYear: moment().subtract(1, 'years').format('YYYY-MM-DD'), // 当前时间往前推一年的时间
      endYear: moment().format('YYYY-MM-DD'),
      date: moment().format('YYYY-MM-DD')
    })
    const subtractDay = () => {
      let $date = moment(state.date).subtract(1, 'days').format('YYYY-MM-DD');
      state.date = $date;
    }
    const change = (index) => {
      state.index = index;
    }
    const addDay = () => {
      let currentDate = moment().format('YYYY-MM-DD');
      if (state.date === currentDate) {
        return;
      }
      let $date = moment(state.date).add(1, 'days').format('YYYY-MM-DD');
      state.date = $date;
    }
    const subtract = (start, end, amount, unit) => {
      let $start = moment(state[start]).subtract(amount, unit).format('YYYY-MM-DD');
      let $end = moment(state[end]).subtract(amount, unit).format('YYYY-MM-DD');
      state[start] = $start;
      state[end] = $end;
    }
    const add = (start, end, amount, unit) => {
      let currentDate = moment().format('YYYY-MM-DD');
      if (state[end] === currentDate) {
        return;
      }
      let $start = moment(state[start]).add(amount, unit).format('YYYY-MM-DD');
      let $end = moment(state[end]).add(amount, unit).format('YYYY-MM-DD');
      state[start] = $start;
      state[end] = $end;
    }

    const subtractWeek = () => {
      subtract('startWeek', 'endWeek', 6, 'days');
    }
    const addWeek = () => {
      add('startWeek', 'endWeek', 6, 'days');
    }
    const subtractMonth = () => {
      subtract('startMonth', 'endMonth', 1, 'months')
    }
    const addMonth = () => {
      add('startMonth', 'endMonth', 1, 'months')
    }
    const subtractYear = () => {
      subtract('startYear', 'endYear', 1, 'years')
    }
    const addYear = () => {
      add('startYear', 'endYear', 1, 'years')
    }
    // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
    return {
      ...toRefs(state),
      rightPress,
      subtractDay,
      addDay,
      addWeek,
      subtractWeek,
      addMonth,
      subtractMonth,
      subtractYear,
      addYear,
    }
  }
}
</script>

