<!--按照年分类的睡眠-->
<template>
  <div class="sleep-Year">
    <p class="date-picker">
      <van-icon name="arrow-left" size="22" color="#999" @click="subtractYear"/>
      <span>{{ startYear.split('-').join('/') }}-{{ endYear.split('-').join('/') }}</span>
      <van-icon name="arrow" size="22" color="#999" @click="addYear"/>
    </p>
    <sleep-local-data
            :time="endYear"
            :hour="locadata.hour"
            :min="locadata.min"
    />
    <div class="div-echarts">
      <v-chart class="chart" :option="option"/>
    </div>
    <div class="div-sleep-calc">
      <sleep-avg-num-and-des
              :data="avgSySleepLog"
      />
      <sleep-proportion
              :option="pieoption"
              :data="piedata"
      />
      <sleep-avg-label
              v-for="(item,index) in bardata" :key="index"
              v-show="item.num"
              :title="item.realname"
              :timeslot="item.reference"
              :status="item.status"
              :backcolor="'#f2f2f2'"
              :onClickLabel="()=>itemonPress(item)"
      />
    </div>
    <sleep-help/>
  </div>
</template>

<script>
  import {reactive, onMounted, toRefs, ref, watch} from 'vue'
  import SleepProportion from '../SleepProportion'
  import SleepHelp from '../sleep-help'
  import SleepAvgLabel from '../SleepAvgLabel'
  import SleepAvgNumAndDes from '../SleepAvgNumAndDes'
  import SleepLocalData from '../SleepLocalData'

  const {getHistorySleepByWeek} = require("../../../../service/sleep");
  import moment from 'moment'

  const {HOST_H5} = require("../../../../utils/axios");
  import {useRouter} from "vue-router";

  export default {
    name: "index",
    components: {
      SleepProportion,
      SleepHelp,
      SleepAvgLabel,
      SleepAvgNumAndDes,
      SleepLocalData,
    },
    props: {
      subtractYear: {
        type: Function,
        default: () => {
        }
      },
      addYear: {
        type: Function,
        default: () => {
        }
      },
      startYear: {
        type: String,
        default: '',
      },
      endYear: {
        type: String,
        default: ''
      },
    },
    setup(props) {
      const years = ['', '', '', '', '', '', '', '', '', '', '', '',];
      const router = useRouter();
      const initData = async () => {
        //根据日获取数据
        //dataSource 0 华为 1 脑路 2  妙健康 3手动录入
        const res = await getHistorySleepByWeek(props.startYear, props.endYear)
        if (res.data && res.data.list && res.data.list.length > 0) {
          // state.avgSySleepLog = res.data.avgSySleepLog.fallsleepduration || 0
          initAvgData(res.data)
          initLocalData(res.data)
          initEchartsBarData(res.data)
          initEchartsPiedata(res.data)
          initother(res.data, flatlist_data)
        } else {
          //清除数据
          clearData()
        }
      }
      const initAvgData = (data) => {
        let fallsleepduration = 0
        let nightsleep = 0
        let average = 0
        for (let i = 0; i < data.list.length; i++) {
          if (data.list[i].sleepType == 0) {
            nightsleep += data.list[i].fallsleepduration || 0
          }
          if (data.list[i].dataSource == 3) {
            fallsleepduration += parseInt((data.list[i].endtime - data.list[i].starttime) / 60000 + '')
            continue
          }
          fallsleepduration += data.list[i].fallsleepduration || 0
        }
        state.avgSySleepLog = fallsleepduration / data.list.length
      }
      const clearData = () => {
        state.locadata.hour = 0
        state.locadata.min = 0

        option.value.series[0].data = (0)
        option.value.series[1].data = (0)
        option.value.series[2].data = (0)
        option.value.series[3].data = (0)
        option.value.series[4].data = (0)
        option.value.series[5].data = (0)
        option.value.series[6].data = (0)

        state.piedata = [
          {color: '#9530F2', name: '深睡', time: '0', proportion: 0},
          {color: '#CA31D9', name: '浅睡', time: '0', proportion: 0},
          {color: '#FFBB20', name: '快速眠动', time: '0', proportion: 0}
        ]
        //设置echarts pie数据
        pieoption.value.series[0].data[0].value = (0)
        pieoption.value.series[0].data[1].value = (0)
        pieoption.value.series[0].data[2].value = (0)

        state.bardata = []
        state.avgSySleepLog = 0
      }
      const flatlist_data = [
        {name: '夜间睡眠', num: 0, unit: '小时', reference: '7-9', status: '', statuscode: -1},
        {name: '深睡比例', num: 0, unit: '%', reference: '20-60', status: '', statuscode: -1},
        {name: '浅睡比例', num: 0, unit: '%', reference: '<50', status: '', statuscode: -1},
        {name: '快速眼动比例', num: 0, unit: '%', reference: '10-30', status: '', statuscode: -1},
        {name: '深睡连续性', num: 0, unit: '分', reference: '70-100', status: '', statuscode: -1},
        {name: '清醒次数', num: 0, unit: '次', reference: '0-2', status: '', statuscode: -1},
        // {name: '呼吸质量', num: 0, unit: '%', reference: '70-100', status: ''},
      ]
      const initLocalData = (data) => {
        //设置当前显示的睡眠时长
        state.locadata.hour = data.list[data.list.length - 1].fallsleepduration ? data.list[data.list.length - 1].fallsleepduration / 60 : 0
        state.locadata.min = data.list[data.list.length - 1].fallsleepduration ? data.list[data.list.length - 1].fallsleepduration % 60 : 0
      }
      const initEchartsBarData = (data) => {
        let lightsleep = [] //浅睡
        let deepsleep = []//深睡
        let wakeduration = [] //清醒
        let rem = []     //快速眠动
        let middle = []     //中睡
        let manual = []     //手动
        let lowsleep = []     //小睡
        let fallsleepduration = []     //睡眠总时长

        for (let i = 0; i < 12; i++) {
          lightsleep.push(0)
          deepsleep.push(0)
          wakeduration.push(0)
          rem.push(0)
          middle.push(0)
          manual.push(0)
          lowsleep.push(0)
          fallsleepduration.push(0)
        }
        for (let i = 0; i < data.list.length; i++) {
          let timedat = new Date(data.list[i].endtime)
          let date = moment(new Date(data.list[i].endtime)).format("YYYY-MM-DD")

          const index_month = timedat.getMonth()
          lightsleep[index_month] += data.list[i].lightsleep || 0
          deepsleep[index_month] += data.list[i].deepsleep || 0
          wakeduration[index_month] += data.list[i].wakeduration || 0
          rem[index_month] += data.list[i].rem || 0
          middle[index_month] += data.list[i].middle || 0
          if (data.list[i].dataSource == 3) {
            manual[index_month] += parseInt((data.list[i].endtime - data.list[i].starttime) / 60000 + '')
          }
          years[index_month] = date
          if (data.list[i].sleepType == 1) {
            lowsleep[index_month] += parseInt(data.list[i].fallsleepduration || '0')
          }
          fallsleepduration[index_month] += parseInt(data.list[i].fallsleepduration || '0')

        }
        option.value.series[0].data = (wakeduration)
        option.value.series[1].data = (lightsleep)
        option.value.series[2].data = (middle)
        option.value.series[3].data = (deepsleep)
        option.value.series[4].data = (manual)
        option.value.series[5].data = (lowsleep)
        option.value.series[6].data = (rem)
        // state.optiondata.qianshui=(fallsleepduration)

      }
      const initEchartsPiedata = (data) => {
        let lightsleep = 0
        let deepsleep = 0
        let rem = 0
        if (data && data.list) {
          for (let i = 0; i < data.list.length; i++) {
            lightsleep += data.list[i].lightsleep || 0
            deepsleep += data.list[i].deepsleep || 0
            rem += data.list[i].rem || 0
          }
          //设置echarts pie右侧数据
          let _piedata = state.piedata
          _piedata[0].time = deepsleep || 0
          _piedata[1].time = lightsleep || 0
          _piedata[2].time = rem || 0

          let sum = deepsleep + lightsleep + rem
          _piedata[0].proportion = sum != 0 ? deepsleep / sum : 0
          _piedata[1].proportion = sum != 0 ? lightsleep / sum : 0
          _piedata[2].proportion = sum != 0 ? rem / sum : 0
          state.piedata = _piedata
          //设置echarts pie数据
          pieoption.value.series[0].data[0].value = (deepsleep)
          pieoption.value.series[0].data[1].value = (lightsleep)
          pieoption.value.series[0].data[2].value = (rem)

        }

      }
      const initother = (data, bardata) => {
        let nightsleep = 0 //夜间睡眠
        let nightdays = 0 //夜间睡眠天数
        let lightsleep = 0 //浅睡
        let deepsleep = 0  //深睡
        let rem = 0        //眠动
        let deepsleepcontinuity = 0        //深睡连续性
        let deepsleepcontinuity_num = 0        //深睡连续性统计的次数
        let wakes = 0        //清醒次数
        let wakes_num = 0        //清醒次数统计的次数
        let fallsleepduration = 0 //总时长
        if (data && data.list) {
          for (let i = 0; i < data.list.length; i++) {
            if (data.list[i].sleepType == 0) {
              nightsleep += data.list[i].fallsleepduration || 0
              nightdays++
            }
            lightsleep += data.list[i].lightsleep || 0
            deepsleep += data.list[i].deepsleep || 0
            rem += data.list[i].rem || 0
            deepsleepcontinuity += data.list[i].deepsleepcontinuity || 0
            if (data.list[i].deepsleepcontinuity) {
              deepsleepcontinuity_num++
            }
            wakes += data.list[i].wakes || 0
            if (data.list[i].wakes) {
              wakes_num++
            }
            fallsleepduration += data.list[i].fallsleepduration || 0
          }
          if (nightdays != 0) {
            let num = parseInt(nightsleep / nightdays + '')
            bardata[0].num = num
            setStatus(bardata, 0, 420, 540)
            setName(bardata[0])
          }

          bardata[1].num = parseInt(deepsleep / fallsleepduration * 100 + '')
          setStatus(bardata, 1, 20, 60)
          setName(bardata[1])


          bardata[2].num = parseInt(lightsleep / fallsleepduration * 100 + '')
          setStatus(bardata, 2, 0, 50)
          setName(bardata[2])

          bardata[3].num = parseInt(rem / fallsleepduration * 100 + '')
          setStatus(bardata, 3, 10, 30)
          setName(bardata[3])

          if (deepsleepcontinuity_num != 0) {
            bardata[4].num = parseInt(deepsleepcontinuity / deepsleepcontinuity_num + '')
            setStatus(bardata, 4, 70, 100)
            setName(bardata[4])
          }

          if (wakes_num != 0) {
            bardata[5].num = parseInt(wakes / wakes_num + '')
            setStatus(bardata, 5, 0, 2)
            setName(bardata[5])
          }

          state.bardata = (JSON.parse(JSON.stringify(bardata)))
        }
      }
      const setStatus = (bardata, index, min, max) => {
        if (bardata[index].num < min) {
          bardata[index].status = '偏低'
          bardata[index].statuscode = 0
        } else if (bardata[index].num > max) {
          bardata[index].status = '偏高'
          bardata[index].statuscode = 2
        } else {
          bardata[index].status = '正常'
          bardata[index].statuscode = 1
        }
      }
      const setName = (bardata) => {
        if (bardata.num) {
          if (bardata.name == '夜间睡眠' || bardata.name == '平均夜间睡眠') {
            bardata.realname = bardata.name + parseInt(bardata.num / 60 + '') + '小时' + parseInt(bardata.num % 60 + '') + '分钟'

          } else {
            bardata.realname = bardata.name + bardata.num + bardata.unit
          }
        }
      }
      onMounted(
              async () => {
                initData();
              }
      )

      watch(() => props.startYear, (news, old) => {
        initData()
      })

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        selectdate: new Date(),
        optiondata: {
          qingxing: [0, 0, 0, 0, 0, 0, 0],
          qianshui: [0, 0, 0, 0, 0, 0, 0],
          zhongshui: [0, 0, 0, 0, 0, 0, 0],
          shenshui: [0, 0, 0, 0, 0, 0, 0],
          shoudong: [0, 0, 0, 0, 0, 0, 0],
          xiaoshui: [0, 0, 0, 0, 0, 0, 0],
          miandong: [0, 0, 0, 0, 0, 0, 0],
        },
        bardata: [],
        piedata: [
          {color: '#9530F2', name: '深睡', time: '0', proportion: 0},
          {color: '#CA31D9', name: '浅睡', time: '0', proportion: 0},
          {color: '#FFBB20', name: '快速眠动', time: '0', proportion: 0}
        ],
        avgSySleepLog: 0,//平均睡眠
        locadata: {hour: 0, min: 0},
      })
      const onchange = (data) => {
        state.selectdate = data
      }


      const option = ref({
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          },
          formatter: function (params) {
            let list = []
            let listItem = ''
            for (let i = 0; i < params.length; i++) {
              list.push(
                      '<i style="display: inline-block;width: 10px;height: 10px;background: ' +
                      params[i].color +
                      ';margin-right: 5px;border-radius: 50%;}"></i><span style="width:70px; display:inline-block;">' +
                      params[i].seriesName +
                      '</span>' +
                      params[i].value
              )
            }
            listItem = list.join('<br>')
            return '<div class="showBox">' +
                    `<p>${params[0].name}</p>`
                    + listItem +
                    '</div>'
          }
        },
        legend: {
          data: [
            '清醒',
            '浅睡',
            '中睡',
            '深睡',
            '手动录入',
            '零星小睡',
            '快速眼动'
          ],
          icon: 'circle',
          padding: 10,
          textStyle: { //图例文字的样式
            fontSize: 10,
          },

          itemHeight: 10,
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
            axisLabel: {interval: 0}
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '清醒',
            type: 'bar',
            stack: 'sleep',
            emphasis: {
              focus: 'series'
            },
            data: state.optiondata.qingxing
          },
          {
            name: '浅睡',
            type: 'bar',
            stack: 'sleep',
            emphasis: {
              focus: 'series'
            },
            data: state.optiondata.qianshui
          },
          {
            name: '中睡',
            type: 'bar',
            stack: 'sleep',
            emphasis: {
              focus: 'series'
            },
            data: state.optiondata.zhongshui
          },
          {
            name: '深睡',
            type: 'bar',
            stack: 'sleep',
            emphasis: {
              focus: 'series'
            },
            data: state.optiondata.shenshui
          },
          {
            name: '手动录入',
            type: 'bar',
            stack: 'sleep',
            emphasis: {
              focus: 'series'
            },
            data: state.optiondata.shoudong
          },
          {
            name: '零星小睡',
            type: 'bar',
            stack: 'sleep',
            emphasis: {
              focus: 'series'
            },
            data: state.optiondata.xiaoshui
          },
          {
            name: '快速眼动',
            type: 'bar',
            stack: 'sleep',
            emphasis: {
              focus: 'series'
            },
            data: state.optiondata.miandong
          },
        ]
      })
      const pieoption = ref({
        title: {
          text: "睡眠",
          subtext: "比例",
          left: "center",
          top: 40,
          textStyle: {
            fontSize: 12,
            fontWeight: 'normal',
            width: 20,
          },

          subtextStyle: {
            fontSize: 12,
            color: '#333333'
          }
        },
        color: ['#C394F2', '#E4A6F3', '#FFBB20',],
        series: [
          {
            name: '睡眠比例',
            type: 'pie',
            radius: ['50%', '80%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'inner',  // 设置标签位置，默认在饼状图外 可选值：'outer' ¦ 'inner（饼状图上）'
                // formatter: '{a} {b} : {c}个 ({d}%)'   设置标签显示内容 ，默认显示{b}
                // {a}指series.name  {b}指series.data的name
                // {c}指series.data的value  {d}%指这一部分占总数的百分比
                formatter: '{c}'
              }

            },
            itemStyle: {
              normal: {
                borderWidth: 4,
                borderColor: '#ffffff',
              }
            },
            labelLine: {
              normal: {
                show: false   // show设置线是否显示，默认为true，可选值：true ¦ false
              }
            },
            data: [
              {value: 0, name: '深睡'},
              {value: 0, name: '浅睡'},
              {value: 0, name: '快速眠动'}
            ]
          }
        ],
      });
      const goTo = (r, query) => {
        router.push({path: r, query: query || {}})
      }
      //标签点击跳转h5
      const itemonPress = (item) => {
        if (item.name === '夜间睡眠' || item.name === '平均夜间睡眠') {
          let hour = Math.floor(item.num / 60);
          let minutes = item.num % 60;
          // let statusCode = hour < 6 ? 0 : hour > 10 ? 2 : 1;
          let url = HOST_H5 + `/sleep/#/dayNight/${'7-9'}/${hour}/${minutes}/${item.statuscode}`
          goTo('/sleep-night-detail', {url});

        }
        if (item.name === '深睡比例' || item.name === '平均深睡比例') {
          let url = HOST_H5 + '/sleep/#/deepSleep/' + item.reference + '/' + item.num + item.unit + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '浅睡比例' || item.name === '平均浅睡比例') {
          let url = HOST_H5 + '/sleep/#/shallowSleep/' + item.num + item.unit + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '快速眼动比例' || item.name === '平均快速眼动比例') {
          let url = HOST_H5 + '/sleep/#/fastEyesRate/' + item.reference + '/' + item.num + item.unit + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '深睡连续性' || item.name === '平均深睡连续性') {
          let url = HOST_H5 + '/sleep/#/deepSleepContinuance/' + item.reference + '/' + item.num + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '清醒次数' || item.name === '平均清醒次数') {
          let url = HOST_H5 + '/sleep/#/wakeTimes/' + item.reference + '/' + item.num + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
        if (item.name === '呼吸质量') {
          let url = HOST_H5 + '/sleep/#/breathQuality/' + item.reference + '/' + item.num + item.unit + '/' + item.statuscode
          goTo('/sleep-night-detail', {url});
        }
      }
      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        onchange,
        option,
        pieoption,
        itemonPress,
      }
    }
  }
</script>

<style scoped lang="less">
