    text-align: center;
  }

  .p-sleep-duration {
    text-align: center;

    .hour {
      font-size: 32px;
      font-weight: bold;
    }

    .minutes {
      font-size: 32px;
      font-weight: bold;
    }
  }


  .list-row {
    display: flex;
    flex-direction: column;
    height: 80px;
    margin: 0 10px 0 10px;
    .border-1px(#ddd);
    justify-content: center;

    .list-row-wrap {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      .list-row-left {
        display: flex;
        flex-direction: column;

        .title {
          font-size: 13px;
          color: #333;
        }

        .refer {
          margin-top: 5px;
          color: #999;
          font-size: 13px;
        }
      }

      .list-row-right {
        display: flex;
        flex-direction: row;
        align-items: center;

        .img-forward {
          display: inline-block;
          width: 7px;
          margin-left: 5px;
          height: 15px;
        }

        .span-status {
          font-size: 13px;
