<!--睡眠当前时间-->
<template>
    <div>
        <p class="p-date">
            <span>{{time}}</span>
        </p>
        <p class="p-sleep-duration"><span><span class="hour">{{hour}}</span>小时<span class="minutes">{{min}}</span>分</span></p>
    </div>
</template>

<script>
  import {reactive, onMounted, toRefs} from 'vue'

  export default {
    name: "SleepLocalData",
    props:{
      time: { // 时间
        type: String,
        default: '',
      },
      hour: { // 小时
        type: String,
        default: '',
      },
      min: { // 分钟
        type: String,
        default: '',
      },
    },
    components: {
    },
    setup() {
      onMounted(
        () => {
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        todo: '',
      })

      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
      }
    }
  }
</script>

