<!--圆形进度条-->
<template>
    <div>
        <svg style="transform: rotate(-90deg)" :width="width" :height="width">
            <circle :r="(width-radius)/2"
                    :cy="width/2"
                    :cx="width/2"
                    :stroke-width="radius"
                    :stroke="backgroundColor"
                    fill="none"
            ></circle>
            <circle ref="$bar"
                    id="bar"
                    :r="(width-radius)/2"
                    :cy="width/2"
                    :cx="width/2"
                    :stroke="barColor"
                    :stroke-width="radius"
                    :stroke-linecap="isRound ? 'round' : 'square'"
                    :stroke-dasharray="(width-radius)*3.14"
                    :stroke-dashoffset="isAnimation ? (width-radius) * 3.14 : (width - radius) * 3.14 * (100 - mprogress) / 100"
                    fill="none"
            >
            </circle>
        </svg>
    </div>
</template>

<script>
  import {onMounted, onBeforeUnmount, reactive, toRefs,watch} from 'vue'

  export default {
    props: {
      width: [Number, String], // 圆的大小
      radius: [Number, String], // 进度条厚度
      progress: [Number, String], // 进度条百分比
      barColor: String, // 进度条颜色
      backgroundColor: String, // 背景颜色
      isAnimation: { // 是否是动画效果
        type: Boolean,
        default: true,
      },
      isRound: { // 是否是圆形画笔
        type: Boolean,
        default: true,
      },
      id: { // 组件的id，多组件共存时使用
        type: [String, Number],
        default: 1,
      },
      duration: { // 整个动画时长
        type: [String, Number],
        default: 1000,
      },
      delay: { // 延迟多久执行
        type: [String, Number],
        default: 200,
      },
      timeFunction: { // 动画缓动函数
        type: String,
        default: 'cubic-bezier(0.99, 0.01, 0.22, 0.94)',
      },
    },
    setup(props) {
      const startanimation = (isAnimation, progress) =>{
        if (isAnimation) {
          // 重复定义判断
          if (document.getElementById(props.idStr)) {
            console.warn('vue-circle-progress should not have same id style')
            document.getElementById(props.idStr).remove()
          }
          // 生成动画样式文件
          let style = document.createElement('style')
          style.id = props.idStr
          style.type = 'text/css'
          style.innerHTML = `
      @keyframes circle_progress_keyframes_name_${props.id} {
      from {stroke-dashoffset: ${(props.width - props.radius) * 3.14}px;}
      to {stroke-dashoffset: ${(props.width - props.radius) * 3.14 * (100 - progress) / 100}px;}}
      .circle_progress_bar${props.id} {animation: circle_progress_keyframes_name_${props.id} ${props.duration}ms ${props.delay}ms ${props.timeFunction} forwards;}`
          // 添加新样式文件
          document.getElementsByTagName('head')[0].appendChild(style)
          // 往svg元素中添加动画class
          document.getElementById('bar').classList.add(`circle_progress_bar${props.id}`)
        }
      }
      onMounted(
        () => {
          startanimation(true,0)
        }
      )

      watch(()=>props.progress,(news,old)=>{
        state.mprogress = news
        startanimation(true,news)
      })

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        idStr: `circle_progress_keyframes_${props.id}`,
        mprogress:0
      })
      onBeforeUnmount(() => {
        // 清除旧组件的样式标签
        document.getElementById(state.idStr) && document.getElementById(props.idStr).remove()
      })
      return {
        ...toRefs(state)
      }
    },
  }
</script>
